/* Add this to your APP.css or relevant stylesheet */
.copy-button {
  cursor: pointer;
  /* Set initial button color to white */
  background-color: #ffffff;
  color: #0080C4;
  border: 1px solid #0080C4; /* Add a border for better visibility */
  padding: 5px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  margin: 4px 2px;
  transition-duration: 0.3s;
}

/* Change button color to green on hover */
.copy-button:hover {
  background-color: #0080C4;
  color: #ffffff; /* Change text color on hover */
}
.button4 {
  background-color: #151515;
  color: #8C9A9A;
  font-size: 14px;
  font-weight: bold;
  border: 0px solid #373737;
  padding: 5px 15px; /* Adjust padding as needed */
  border-radius: 8px;
  transition: 0.5s;
  text-decoration: none; /* Remove underline */
  display: inline-block; /* Display as inline-block to take padding and border */
}
.button4:hover {background-color: #373737;

}
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

/* Apply animation to the Changelog link */
.button4.animation {
  animation: pulse 2s infinite;
}
.itemCategory {
  transition: transform 0.3s;
}

.itemCategory:hover {
  transform: translateX(4px);
}
.notice {
  color: red;
